/* ===============================
   WATCH & SHOP SECTION
================================ */

.watchshop {
  --ws-maroon: #8b0d2b;
  --ws-maroon-deep: #6f0a22;
  --ws-gold: #c99b59;
  padding: 64px 30px 62px;
  background: linear-gradient(180deg, #fff7ef 0%, #fffdf9 34%, #ffffff 100%);
  position: relative;
  overflow: hidden;
}

.watchshop::before,
.watchshop::after {
  content: "";
  position: absolute;
  border-radius: 999px;
  pointer-events: none;
  z-index: 0;
}

.watchshop::before {
  width: 320px;
  height: 320px;
  top: -120px;
  left: -100px;
  background: radial-gradient(circle, rgba(201, 155, 89, 0.2) 0%, rgba(201, 155, 89, 0) 72%);
}

.watchshop::after {
  width: 280px;
  height: 280px;
  right: -96px;
  top: 110px;
  background: radial-gradient(circle, rgba(139, 13, 43, 0.12) 0%, rgba(139, 13, 43, 0) 70%);
}

.watchshop-header {
  max-width: 760px;
  margin: 0 auto 26px;
  text-align: center;
  position: relative;
  z-index: 1;
}

.watchshop-kicker {
  margin: 0;
  font-size: 11px;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: var(--ws-maroon);
}

.watchshop-title {
  text-align: center;
  font-size: clamp(34px, 4vw, 48px);
  margin: 8px 0 8px;
  font-family: "Times New Roman", serif;
  line-height: 1.02;
  color: #20130e;
}

.watchshop-subtitle {
  margin: 0;
  font-size: 14px;
  line-height: 1.55;
  color: #6e5f53;
}

.watchshop .swiper {
  position: relative;
  z-index: 1;
  padding: 8px 34px 10px;
}

/* ===============================
   CARD
================================ */

.watchshop-card {
  background: linear-gradient(180deg, #fffdfb 0%, #fff9f3 100%);
  border-radius: 18px;
  overflow: hidden;
  box-shadow: 0 14px 30px rgba(36, 20, 10, 0.12);
  border: 1px solid #ead7c2;
  transition: transform 0.25s ease, box-shadow 0.25s ease;
  cursor: pointer;
}

/* ===============================
   VIDEO
================================ */

.watchshop-video-wrapper {
  height: 420px;
  cursor: pointer;
  overflow: hidden;
  border-bottom: 1px solid rgba(188, 154, 107, 0.32);
  contain: paint;
  transform: translateZ(0);
  backface-visibility: hidden;
}

.watchshop-video-wrapper video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  transition: transform 0.35s ease;
  transform: translateZ(0);
  will-change: transform;
  backface-visibility: hidden;
}

/* ===============================
   INFO SECTION
================================ */

.watchshop-info {
  padding: 14px 14px 16px;
  text-align: center;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.88) 0%, rgba(255, 250, 242, 1) 100%);
}

.watchshop-price {
  display: grid;
  grid-template-columns: minmax(78px, auto) minmax(78px, auto) minmax(78px, auto);
  justify-content: center;
  align-items: baseline;
  column-gap: 8px;
  font-size: 15px;
  margin-bottom: 6px;
}

.watchshop-price .price {
  font-weight: 600;
  color: #24160f;
  text-align: right;
  white-space: nowrap;
  font-variant-numeric: tabular-nums;
}

.watchshop-price .discount {
  font-size: 12px;
  color: var(--ws-maroon);
  border: 1px solid rgba(139, 13, 43, 0.32);
  border-radius: 999px;
  padding: 2px 6px;
  background: rgba(139, 13, 43, 0.06);
  text-align: center;
  white-space: nowrap;
  font-variant-numeric: tabular-nums;
}

.watchshop-price .old-price {
  font-size: 13px;
  color: #8b7f76;
  text-decoration: line-through;
  text-align: left;
  white-space: nowrap;
  font-variant-numeric: tabular-nums;
}

.watchshop-name {
  font-family: "Times New Roman", serif;
  font-size: 17px;
  font-weight: 600;
  color: #1d130d;
  margin: 8px 0 12px;
}

/* ===============================
   BUY BUTTON
================================ */

.watchshop-btn {
  width: 100%;
  padding: 11px 0;
  background: var(--ws-maroon);
  color: #fff;
  border: none;
  border-radius: 11px;
  font-size: 13px;
  text-transform: uppercase;
  letter-spacing: 0.9px;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.25s ease, transform 0.2s ease;
}

.watchshop-btn:hover {
  background: var(--ws-maroon-deep);
  transform: translateY(-1px);
}

.watchshop-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 20px 36px rgba(33, 19, 10, 0.18);
}

.watchshop-card:hover .watchshop-video-wrapper video {
  transform: scale(1.05);
}




/* ===============================
   CUSTOM SLIDER ARROWS (CLEAN)
================================ */

.watchshop-prev,
.watchshop-next {
  position: absolute;
  top: 43%;
  transform: translateY(-50%);
  width: 42px;
  height: 42px;
  background: rgba(255, 255, 255, 0.95);
  border: 1px solid #e9d8c7;
  border-radius: 50%;
  color: #2f2017;
  font-size: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 10;
  transition: background 0.2s ease, transform 0.2s ease, box-shadow 0.2s ease;
}

.watchshop-prev:hover,
.watchshop-next:hover {
  background: #fff;
  box-shadow: 0 10px 18px rgba(37, 20, 10, 0.16);
  transform: translateY(-50%) scale(1.03);
}

.watchshop-prev {
  left: 2px;
}

.watchshop-next {
  right: 2px;
}



/* MOBILE */
@media (max-width: 768px) {
  .watchshop {
    padding: 44px 14px 44px;
  }

  .watchshop-header {
    margin-bottom: 18px;
  }

  .watchshop-subtitle {
    font-size: 13px;
  }

  .watchshop .swiper {
    padding: 6px 0;
  }

  .watchshop-prev,
  .watchshop-next {
    width: 40px;
    height: 40px;
    font-size: 24px;
    top: 48%;
  }

  .watchshop-prev { left: 6px; }
  .watchshop-next { right: 6px; }
}




/* ===============================
   MOBILE
================================ */

@media (max-width: 768px) {
  .watchshop-video-wrapper {
    height: 520px;
  }

  .swiper-button-prev,
  .swiper-button-next {
    width: 38px;
    height: 38px;
    font-size: 22px;
  }

  .swiper-button-prev {
    left: 6px;
  }

  .swiper-button-next {
    right: 6px;
  }
}

@media (max-width: 480px) {
  .watchshop-title {
    font-size: 34px;
  }

  .watchshop-kicker {
    font-size: 10px;
  }

  .watchshop-subtitle {
    font-size: 12px;
  }

  .watchshop-video-wrapper {
    height: 460px;
  }
}

