.about-section {
  --about-maroon: #8b0d2b;
  --about-maroon-deep: #6f0a22;
  --about-gold: #c79957;
  position: relative;
  padding: 64px 18px 70px;
  background:
    radial-gradient(
      circle at 12% 22%,
      rgba(240, 213, 175, 0.28) 0%,
      rgba(240, 213, 175, 0) 42%
    ),
    linear-gradient(180deg, #fffdfb 0%, #fff6ee 46%, #ffffff 100%);
  overflow: hidden;
}

.about-shell {
  max-width: 1240px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: minmax(0, 1.05fr) minmax(320px, 0.85fr);
  align-items: center;
  gap: 28px;
}

.about-copy {
  background: linear-gradient(180deg, #fffefc 0%, #fff9f2 100%);
  border: 1px solid #ecd8c3;
  border-radius: 22px;
  box-shadow: 0 16px 34px rgba(36, 20, 10, 0.11);
  padding: 28px 28px 26px;
}

.about-kicker {
  margin: 0;
  font-size: 11px;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: var(--about-maroon);
}

.about-title {
  margin: 8px 0 10px;
  font-family: "Times New Roman", serif;
  font-size: clamp(34px, 4.3vw, 52px);
  line-height: 0.98;
  color: #1e130d;
}

.about-lead {
  margin: 0;
  font-size: 15px;
  line-height: 1.58;
  color: #605347;
  max-width: 56ch;
}

.about-content {
  margin-top: 16px;
  position: relative;
  padding-left: 20px;
}

.about-content .quote {
  position: absolute;
  left: -6px;
  top: -18px;
  font-size: 46px;
  color: rgba(139, 13, 43, 0.28);
  font-family: "Times New Roman", serif;
  line-height: 1;
}

.about-content p {
  margin: 0;
  font-family: "Times New Roman", serif;
  font-size: 15px;
  line-height: 1.8;
  letter-spacing: 0.3px;
  color: rgba(24, 15, 10, 0.78);
  text-align: justify;
}

.about-highlights {
  margin-top: 18px;
  display: flex;
  flex-wrap: wrap;
  gap: 0;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.about-highlights span {
  font-size: 12px;
  letter-spacing: 0.4px;
  color: #674e40;
  border: none;
  background: transparent;
  border-radius: 0;
  padding: 0;
}

.about-highlights span + span::before {
  content: " \2022 ";
  color: rgba(103, 78, 64, 0.55);
  margin: 0 8px;
}

.about-actions {
  margin-top: 18px;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  
}

.about-btn {
  border: 1px solid transparent;
  border-radius: 999px;
  padding: 11px 16px;
  font-size: 11px;
  letter-spacing: 1px;
  text-transform: uppercase;
  cursor: pointer;
  transition: transform 0.2s ease, background 0.2s ease, color 0.2s ease,
    border-color 0.2s ease;
    
}

.about-btn.primary {
  background: var(--about-maroon);
  color: #fff;
  border-color: var(--about-maroon);
  
}

.about-btn.primary:hover {
  background: var(--about-maroon-deep);
  border-color: var(--about-maroon-deep);
  transform: translateY(-1px);
}

.about-btn.ghost {
  background: #fff;
  color: #2a1c14;
  border-color: #d9b892;
}

.about-btn.ghost:hover {
  background: #fff6eb;
  transform: translateY(-1px);
}

.about-media {
  display: flex;
  justify-content: center;
}

.about-image-wrap {
  position: relative;
  width: min(520px, 100%);
  border-radius: 26px;
  overflow: hidden;
  box-shadow: 0 24px 44px rgba(26, 14, 8, 0.25);
  border: 1px solid #e8d2ba;
}

.about-image-wrap img {
  width: 100%;
  height: clamp(420px, 55vw, 620px);
  object-fit: cover;
  object-position: center;
  display: block;
}

.about-badge {
  position: absolute;
  left: 16px;
  right: 16px;
  bottom: 16px;
  border: 1px solid rgba(255, 255, 255, 0.48);
  border-radius: 14px;
  background: rgba(23, 14, 9, 0.52);
  backdrop-filter: blur(5px);
  padding: 10px 12px;
  color: #fffaf2;
}

.about-badge strong {
  display: block;
  font-size: 18px;
  font-family: "Times New Roman", serif;
}

.about-badge span {
  display: block;
  margin-top: 2px;
  font-size: 12px;
  color: rgba(255, 244, 227, 0.86);
}

@media (max-width: 1024px) {
  .about-section {
    padding: 52px 18px 56px;
  }

  .about-shell {
    grid-template-columns: 1fr;
    gap: 20px;
  }

  .about-copy {
    padding: 24px 20px 22px;
  }

  .about-content p {
    text-align: left;
  }

  .about-image-wrap {
    width: min(620px, 100%);
  }

  .about-image-wrap img {
    height: min(520px, 68vw);
  }
}

@media (max-width: 640px) {
  .about-section {
    padding: 34px 12px 38px;
  }

  .about-shell {
    gap: 14px;
  }

  .about-copy {
    border-radius: 18px;
    padding: 18px 16px 16px;
  }

  .about-title {
    font-size: clamp(30px, 9vw, 40px);
  }

  .about-lead {
    font-size: 13px;
    line-height: 1.5;
  }

  .about-content {
    margin-top: 12px;
    padding-left: 10px;
  }

  .about-content .quote {
    font-size: 30px;
    top: -10px;
    left: -4px;
  }

  .about-content p {
    font-size: 12.5px;
    line-height: 1.58;
    letter-spacing: 0.1px;
  }

  .about-highlights {
    margin-top: 12px;
    display: flex;
    flex-wrap: wrap;
    gap: 0;
    row-gap: 2px;
  }

  .about-highlights span {
    font-size: 10.5px;
    line-height: 1.35;
  }

  .about-highlights span + span::before {
    content: " \2022 ";
    color: rgba(103, 78, 64, 0.5);
    margin: 0 6px;
  }

  .about-actions {
    margin-top: 12px;
    display: grid;
    grid-template-columns: 1fr;
    gap: 8px;
  }

  .about-btn {
    width: 100%;
    padding: 10px 12px;
    font-size: 10px;
  }

  .about-image-wrap {
    border-radius: 16px;
  }

  .about-image-wrap img {
    height: 190px;
  }

  .about-badge {
    display: none;
  }
}
