/* SECTION */
.signature-section {
  background-color: #d8beb9;
  padding: 80px 60px 50px;
}

/* LAYOUT */
.signature-inner {
  max-width: 1400px;
  margin: auto;
  display: flex;
  align-items: center;
  gap: 120px;
}

/* IMAGE AREA */
.signature-images {
  position: relative;
  width: 520px;
  height: 520px;
}

/* COMMON IMAGE STYLE */
.signature-images .img {
  position: absolute;
  border-radius: 10px;
  object-fit: cover;
  opacity: 0; /* hidden initially */
  box-shadow: 0 12px 30px rgba(0,0,0,0.08);
}

/* LEFT IMAGE  BEHIND CENTER */
.img-left {
  width: 200px;
  height: 280px;
  top: -30px;
  left: -6px;
  z-index: 1;
}

/* CENTER IMAGE  MAIN */
.img-center {
  width: 260px;
  height: 380px;
  top: 60px;
  left: 160px;
  z-index: 3;
}

/* RIGHT IMAGE  BEHIND CENTER */
.img-right {
  width: 200px;
  height: 280px;
  top: 250px;
  left: 390px;
  z-index: 2;
}

/* TEXT */
.signature-text {
  position: relative;
  max-width: 460px;
  margin-left: 130px;
  margin-bottom: 86px;
  padding: 28px 32px 30px;
  border-radius: 20px;
  background: linear-gradient(
    160deg,
    rgba(255, 255, 255, 0.5) 0%,
    rgba(255, 255, 255, 0.24) 52%,
    rgba(247, 226, 219, 0.36) 100%
  );
  border: 1px solid rgba(56, 33, 25, 0.11);
  box-shadow:
    0 20px 36px rgba(63, 37, 27, 0.12),
    inset 0 1px 0 rgba(255, 255, 255, 0.65);
}

.signature-text::before {
  content: "";
  position: absolute;
  right: 16px;
  top: 12px;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  background: radial-gradient(
    circle,
    rgba(139, 26, 57, 0.12) 0%,
    rgba(139, 26, 57, 0) 70%
  );
  pointer-events: none;
}

/* SIGNATURE LABEL */
.signature-kicker {
  font-family: "Cinzel", "Times New Roman", serif;
  font-size: 12px;
  letter-spacing: 2.8px;
  font-weight: 600;
  text-transform: uppercase;
  color: rgba(45, 25, 18, 0.72);
  margin: 0 0 12px;
  display: inline-flex;
  align-items: center;
  gap: 10px;
  position: relative;
  padding: 6px 12px;
  border-radius: 999px;
  border: 1px solid rgba(139, 26, 57, 0.2);
  background: rgba(255, 255, 255, 0.5);
}

/* UNDERLINE */
.signature-kicker::after {
  content: "";
  display: inline-block;
  width: 34px;
  height: 1px;
  background-color: rgba(139, 26, 57, 0.48);
}

/* MAIN HEADING */
.signature-heading {
  font-family: "Playfair Display", "Times New Roman", serif;
  font-size: 52px;
  font-weight: 600;
  line-height: 1.02;
  margin: 0 0 14px;
  color: #2b1a14;
  letter-spacing: 0.2px;
}

.signature-brand {
  color: #7f102f;
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.35);
}

.signature-lead {
  margin: 0 0 14px;
  font-family: "Lora", "Times New Roman", serif;
  font-size: 19px;
  line-height: 1.5;
  color: rgba(38, 22, 17, 0.82);
  font-style: italic;
}

/* DESCRIPTION */
.signature-description {
  font-family: "Lora", "Times New Roman", serif;
  font-size: 16px;
  letter-spacing: 0.15px;
  line-height: 1.88;
  color: rgba(33, 21, 16, 0.78);
  margin: 0;
  padding-left: 14px;
  border-left: 2px solid rgba(139, 26, 57, 0.28);
}

/* ===================== */
/* ANIMATIONS (KEYFRAMES) */
/* ===================== */

@keyframes fromLeftBehind {
  from {
    transform: translateX(-120px);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes fromBottomTop {
  from {
    transform: translateY(120px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes fromRightBehind {
  from {
    transform: translateX(120px);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

/* ===================== */
/* SCROLL TRIGGER */
/* ===================== */

.signature-section .img {
  animation: none;
  opacity: 0;
}

.signature-section.animate .img-left {
  animation: fromLeftBehind 1s ease-out forwards;
}

.signature-section.animate .img-center {
  animation: fromBottomTop 1s ease-out forwards;
  animation-delay: 0.3s;
}

.signature-section.animate .img-right {
  animation: fromRightBehind 1s ease-out forwards;
  animation-delay: 0.6s;
}

@media (max-width: 1024px) {
  .signature-inner {
    flex-direction: column;
    gap: 60px;
  }

  .signature-text {
    margin-left: 0;
    text-align: center;
    padding: 20px 20px 22px;
  }

  .signature-lead {
    font-size: 17px;
  }

  .signature-description {
    border-left: none;
    padding-left: 0;
  }
}




/* ===================================================
   RESPONSIVE ONLY  DESKTOP UNTOUCHED
=================================================== */

/* TABLET & MOBILE */
@media (max-width: 768px) {

  .signature-section {
    padding: 60px 20px 40px;
  }

  .signature-inner {
    flex-direction: column;
    gap: 50px;
  }

  /* SCALE IMAGE GROUP */
  .signature-images {
    width: 320px;
    height: 360px;
    margin: 0 auto;
  }

  .img-left {
    width: 140px;
    height: 200px;
    top: 0;
    left: 0;
  }

  .img-center {
    width: 180px;
    height: 260px;
    top: 50px;
    left: 70px;
  }

  .img-right {
    width: 140px;
    height: 200px;
    top: 150px;
    left: 180px;
  }

  .signature-text {
    margin: 0;
    text-align: center;
    max-width: 100%;
    padding: 18px 16px 20px;
  }

  .signature-kicker {
    justify-content: center;
    font-size: 11px;
    letter-spacing: 2px;
  }

  .signature-kicker::after {
    width: 26px;
  }

  .signature-heading {
    font-size: 37px;
  }

  .signature-lead {
    font-size: 15px;
    line-height: 1.55;
  }

  .signature-description {
    font-size: 14px;
    line-height: 1.75;
  }
}

/* SMALL PHONES */
@media (max-width: 480px) {

  .signature-images {
    width: 280px;
    height: 320px;
  }

  .img-left {
    width: 120px;
    height: 180px;
  }

  .img-center {
    width: 160px;
    height: 240px;
    left: 60px;
  }

  .img-right {
    width: 120px;
    height: 180px;
    left: 160px;
  }

  .signature-heading {
    font-size: 31px;
  }

  .signature-lead {
    font-size: 14px;
  }
}

