.placeorder-page {
  background: linear-gradient(180deg, #f7f4ef 0%, #f3ede4 100%);
  padding: 28px 0 56px;
  font-family: "Times New Roman", serif;
}

.placeorder-intro {
  max-width: 1200px;
  margin: 0 auto 20px;
  padding: 0 18px;
}

.placeorder-intro .eyebrow {
  margin: 0;
  font-size: 12px;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #8b0d2b;
}

.placeorder-intro h1 {
  margin: 8px 0 6px;
  font-size: 42px;
  font-weight: 500;
  color: #20160f;
  line-height: 1.02;
}

.placeorder-intro .subtext {
  margin: 0;
  font-size: 15px;
  color: #5f5349;
}

.placeorder-wrapper {
  max-width: 1200px;
  margin: auto;
  display: grid;
  grid-template-columns: minmax(0, 1.35fr) minmax(320px, 0.8fr);
  gap: 26px;
  padding: 0 18px;
}

.placeorder-left,
.placeorder-right {
  background: rgba(255, 255, 255, 0.9);
  border: 1px solid #e6d8c7;
  border-radius: 20px;
  box-shadow: 0 14px 28px rgba(40, 22, 12, 0.06);
}

.placeorder-left {
  padding: 24px;
}

.section-head {
  margin-bottom: 10px;
}

.section-head h2 {
  margin: 0;
  font-size: 29px;
  font-weight: 500;
  color: #1f1510;
}

.section-head p {
  margin: 8px 0 0;
  font-size: 13px;
  color: #706257;
}

.placeorder-left label {
  display: block;
  font-size: 12px;
  margin: 14px 0 6px;
  letter-spacing: 1.3px;
  text-transform: uppercase;
  color: #6a5b50;
}

.placeorder-left input,
.placeorder-left select {
  width: 100%;
  padding: 13px 14px;
  background: #fbf8f4;
  border: 1px solid #e1d4c4;
  border-radius: 10px;
  margin-bottom: 2px;
  font-size: 14px;
  color: #1f1f1f;
  transition: border-color 0.2s ease, box-shadow 0.2s ease, background 0.2s ease;
}

.placeorder-left input:focus,
.placeorder-left select:focus {
  outline: none;
  border-color: #8b0d2b;
  box-shadow: 0 0 0 3px rgba(139, 13, 43, 0.12);
  background: #fff;
}

.placeorder-left input:disabled,
.placeorder-left select:disabled {
  background: #f0ece7;
  color: #847568;
  cursor: not-allowed;
}

.place-select {
  position: relative;
  margin-bottom: 2px;
}

.place-select-trigger {
  width: 100%;
  min-height: 48px;
  padding: 12px 42px 12px 14px;
  border: 1px solid #e1d4c4;
  border-radius: 10px;
  background: #fbf8f4;
  color: #1f1f1f;
  font-size: 14px;
  text-align: left;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
  cursor: pointer;
  transition: border-color 0.2s ease, box-shadow 0.2s ease, background 0.2s ease;
}

.place-select-trigger:focus {
  outline: none;
  border-color: #8b0d2b;
  box-shadow: 0 0 0 3px rgba(139, 13, 43, 0.12);
  background: #fff;
}

.place-select.invalid .place-select-trigger {
  border-color: #b00020;
}

.place-select.disabled .place-select-trigger,
.place-select-trigger:disabled {
  background: #f0ece7;
  color: #847568;
  cursor: not-allowed;
}

.place-select-placeholder {
  color: #847568;
}

.place-select-caret {
  position: absolute;
  right: 14px;
  top: 50%;
  transform: translateY(-50%);
  color: #5d4f44;
  font-size: 13px;
  pointer-events: none;
}

.place-select-panel {
  position: absolute;
  left: 0;
  right: 0;
  top: calc(100% + 6px);
  z-index: 40;
  background: #fffdfb;
  border: 1px solid #e1d4c4;
  border-radius: 12px;
  box-shadow: 0 14px 26px rgba(28, 15, 8, 0.12);
  padding: 8px;
}

.place-select-search {
  width: 100%;
  border: 1px solid #e1d4c4;
  border-radius: 8px;
  padding: 10px 11px;
  font-size: 13px;
  margin-bottom: 8px;
  background: #fff;
}

.place-select-search:focus {
  outline: none;
  border-color: #8b0d2b;
}

.place-select-options {
  max-height: 220px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.place-select-option {
  width: 100%;
  border: none;
  border-radius: 8px;
  background: transparent;
  text-align: left;
  font-size: 14px;
  color: #2e241d;
  padding: 9px 10px;
  cursor: pointer;
  transition: background 0.2s ease, color 0.2s ease;
}

.place-select-option:hover {
  background: #f7eee5;
}

.place-select-option.selected {
  background: #8b0d2b;
  color: #fff;
}

.place-select-empty {
  padding: 10px;
  font-size: 13px;
  color: #6e5f54;
}

.saved-addresses {
  margin: 8px 0 18px;
  padding: 14px;
  border: 1px solid #eadfd5;
  border-radius: 14px;
  background: linear-gradient(180deg, #fffefb 0%, #fff9f3 100%);
}

.saved-addresses-header {
  display: flex;
  align-items: baseline;
  justify-content: space-between;
  gap: 12px;
  margin-bottom: 12px;
  font-size: 12px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #6b3a2a;
}

.saved-addresses-hint {
  text-transform: none;
  letter-spacing: 0;
  font-size: 12px;
  color: #8a7d75;
}

.saved-addresses-grid {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 12px;
}

.saved-address-card {
  display: grid;
  grid-template-columns: 1fr 18px;
  gap: 12px;
  padding: 12px;
  border: 1px solid #eadfd6;
  border-radius: 12px;
  background: #fff;
  cursor: pointer;
  position: relative;
  transition: border-color 0.2s ease, box-shadow 0.2s ease, transform 0.2s ease;
}

.saved-address-card:hover {
  border-color: #8b0d2b;
  box-shadow: 0 8px 16px rgba(139, 13, 43, 0.08);
  transform: translateY(-1px);
}

.saved-address-card input {
  position: absolute;
  opacity: 0;
  pointer-events: none;
}

.saved-address-content {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.saved-address-title {
  font-size: 14px;
  font-weight: 600;
  color: #2c2420;
}

.saved-address-line {
  font-size: 12.5px;
  color: #4b3f39;
}

.saved-address-line.muted {
  color: #7d6e66;
}

.saved-address-check {
  width: 14px;
  height: 14px;
  border-radius: 50%;
  border: 1px solid #c9bdb5;
  margin-top: 4px;
  position: relative;
  justify-self: end;
}

.saved-address-card.selected {
  border-color: #8b0d2b;
  background: #fff7f8;
}

.saved-address-card.selected .saved-address-check {
  border-color: #8b0d2b;
}

.saved-address-card.selected .saved-address-check::after {
  content: "";
  position: absolute;
  inset: 3px;
  background: #8b0d2b;
  border-radius: 50%;
}

.checkbox-row,
.radio-row {
  display: flex;
  align-items: center;
  gap: 10px;
  margin: 12px 0 18px;
  font-size: 13px;
  color: #66584f;
  cursor: pointer;
  user-select: none;
}

.checkbox-row input,
.radio-row input {
  margin: 0;
  width: 16px;
  height: 16px;
  cursor: pointer;
}

.error-text {
  color: #b00020;
  font-size: 12px;
  margin: 4px 0 2px;
}

.field-note {
  font-size: 12px;
  margin: 4px 0 8px;
  color: #6b6158;
}

.field-note.error {
  color: #b00020;
}

.field-note-with-action {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
}

.field-note-action {
  border: none;
  background: transparent;
  color: #8b0d2b;
  font-size: 12px;
  letter-spacing: 0.8px;
  text-transform: uppercase;
  cursor: pointer;
  padding: 0;
  text-decoration: underline;
  text-underline-offset: 2px;
}

.field-note-action:hover:not(:disabled) {
  color: #6e0b22;
}

.field-note-action:disabled {
  opacity: 0.65;
  cursor: not-allowed;
}

.section-title {
  font-size: 20px;
  margin: 22px 0 12px;
  font-weight: 600;
  color: #221711;
}

.payment-box {
  display: grid;
  grid-template-columns: 1fr;
  margin-top: 8px;
}

.payment-card {
  display: flex;
  align-items: center;
  gap: 12px;
  border: 1px solid #d8c7b4;
  border-radius: 12px;
  padding: 12px 14px;
  font-size: 14px;
  cursor: pointer;
  color: #352920;
  background: #fffaf5;
  transition: border-color 0.22s ease, box-shadow 0.22s ease, background 0.22s ease;
}

.payment-card input[type="radio"] {
  width: 16px;
  height: 16px;
  accent-color: #8b0d2b;
  cursor: pointer;
}

.payment-card:hover {
  border-color: #8b0d2b;
}

.payment-card.selected {
  border-color: #8b0d2b;
  background: #fff5f8;
  box-shadow: 0 8px 18px rgba(139, 13, 43, 0.1);
}

.payment-copy {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.payment-title {
  font-size: 15px;
  font-weight: 600;
  color: #2a1d16;
}

.payment-sub {
  font-size: 12px;
  color: #7b6a5f;
}

.placeorder-btn {
  width: 100%;
  margin-top: 20px;
  padding: 14px 16px;
  border: 1px solid #7f0d32;
  border-radius: 12px;
  background: linear-gradient(135deg, #7f0d32 0%, #a71043 100%);
  color: #fff;
  font-size: 13px;
  letter-spacing: 2.1px;
  cursor: pointer;
  transition: filter 0.25s ease, transform 0.2s ease;
}

.placeorder-btn:hover {
  filter: brightness(1.05);
  transform: translateY(-1px);
}

/* RIGHT */
.placeorder-right {
  padding: 24px;
  position: sticky;
  top: 94px;
  height: fit-content;
}

.placeorder-right h3 {
  margin: 0 0 14px;
  font-size: 28px;
  font-weight: 500;
  color: #21160f;
}

.order-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 16px;
}

.order-empty {
  border: 1px dashed #d5c4b2;
  border-radius: 10px;
  padding: 12px;
  font-size: 13px;
  color: #6f6156;
  background: #fff9f1;
}

.order-item {
  display: flex;
  gap: 12px;
  padding: 10px;
  border: 1px solid #eadfd3;
  border-radius: 12px;
  background: #fff;
}

.order-item img {
  width: 62px;
  height: 80px;
  object-fit: cover;
  border-radius: 8px;
  border: 1px solid #efe2d4;
}

.order-item p {
  margin: 0;
  font-size: 13px;
  line-height: 1.35;
  color: #2e221b;
}

.order-item p + p {
  margin-top: 4px;
  color: #6f6156;
}

.coupon-box {
  display: flex;
  gap: 8px;
  margin: 12px 0 8px;
}

.page-confetti {
  position: fixed;
  inset: 0;
  pointer-events: none;
  z-index: 3000;
  overflow: hidden;
}

.page-confetti-flash {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 42vmin;
  height: 42vmin;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  background: radial-gradient(
    circle,
    rgba(255, 255, 255, 0.22) 0%,
    rgba(201, 135, 61, 0.18) 30%,
    rgba(127, 13, 50, 0.14) 52%,
    transparent 72%
  );
  animation: confetti-flash 520ms ease-out forwards;
}

.confetti-piece {
  position: absolute;
  left: var(--x-start);
  top: -10vh;
  width: var(--piece-size);
  height: calc(var(--piece-size) * 0.48);
  background: var(--piece-color);
  opacity: var(--piece-opacity);
  transform: translateX(-50%) rotate(0deg);
  box-shadow: 0 0 0 1px rgba(255, 255, 255, 0.12);
  animation:
    confetti-fall var(--fall-duration) cubic-bezier(0.2, 0.72, 0.25, 0.98)
      forwards,
    confetti-spin var(--spin-duration) linear infinite;
  animation-delay: var(--fall-delay), var(--fall-delay);
}

.confetti-piece.shape-1 {
  border-radius: 999px;
}

.confetti-piece.shape-2 {
  clip-path: polygon(50% 0, 0 100%, 100% 100%);
  width: calc(var(--piece-size) * 0.92);
  height: calc(var(--piece-size) * 0.85);
}

.coupon-box input {
  flex: 1;
  padding: 12px;
  background: #f7f3ed;
  border: 1px solid #ddcfbe;
  border-radius: 10px;
}

.coupon-box button {
  padding: 12px 14px;
  border: 1px solid #2a1d16;
  border-radius: 10px;
  background: #fff;
  cursor: pointer;
  transition: all 0.25s ease;
  letter-spacing: 1px;
}

.coupon-box button:hover {
  background: #231912;
  color: #fff;
}

.coupon-box.success input {
  background: #edf8ef;
  border-color: #9cc8a0;
}

.coupon-box.error input {
  background: #fdf0ef;
  border-color: #ebb4ae;
}

.coupon-message {
  font-size: 12px;
  margin: 4px 0 10px;
  animation: fadeIn 0.35s ease;
}

.coupon-message.success {
  color: #2e7d32;
}

.coupon-message.error {
  color: #c62828;
}

@keyframes confetti-fall {
  0% {
    opacity: 0;
    transform: translateX(-50%) translateY(-8vh) rotate(0deg);
  }
  8% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: translateX(calc(-50% + var(--x-drift))) translateY(112vh)
      rotate(600deg);
  }
}

@keyframes confetti-spin {
  from {
    filter: saturate(1) brightness(1);
  }
  to {
    filter: saturate(1.15) brightness(1.05);
  }
}

@keyframes confetti-flash {
  0% {
    opacity: 0;
    transform: translate(-50%, -50%) scale(0.5);
  }
  30% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: translate(-50%, -50%) scale(1.15);
  }
}

.summary {
  border: 1px solid #e2d4c4;
  border-radius: 14px;
  padding: 12px;
  background: #fffaf4;
}

.summary div {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  margin-bottom: 10px;
  font-size: 14px;
  color: #46392f;
}

.summary div:last-child {
  margin-bottom: 0;
}

.summary .discount-row {
  color: #2e7d32;
  font-weight: 600;
}

.summary .total {
  margin-top: 10px;
  padding-top: 10px;
  border-top: 1px solid #ddcfbe;
  font-weight: 700;
  color: #1f1510;
}

.summary .total span:last-child {
  font-size: 20px;
  color: #8b0d2b;
}

.checkout-assurance {
  margin-top: 12px;
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 8px;
}

.checkout-assurance span {
  text-align: center;
  font-size: 11px;
  color: #66584f;
  border: 1px solid #ddcfbe;
  border-radius: 999px;
  padding: 7px 8px;
  background: #fff;
}

.animate-pop {
  animation: pop 0.4s ease;
}

@keyframes pop {
  0% {
    transform: scale(0.95);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-4px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (max-width: 1024px) {
  .placeorder-wrapper {
    grid-template-columns: 1fr;
  }

  .placeorder-right {
    position: static;
  }
}

@media (max-width: 768px) {
  .placeorder-page {
    padding: 18px 0 36px;
  }

  .placeorder-intro {
    padding: 0 14px;
    margin-bottom: 12px;
  }

  .placeorder-intro h1 {
    font-size: 30px;
  }

  .placeorder-intro .subtext {
    font-size: 14px;
  }

  .placeorder-wrapper {
    gap: 16px;
    padding: 0 14px;
  }

  .placeorder-left,
  .placeorder-right {
    border-radius: 16px;
    padding: 16px;
  }

  .section-head h2 {
    font-size: 24px;
  }

  .saved-addresses-grid {
    grid-template-columns: 1fr;
  }

  .placeorder-left input,
  .placeorder-left select {
    padding: 12px;
    font-size: 13px;
  }

  .place-select-trigger {
    min-height: 44px;
    padding: 11px 38px 11px 12px;
    font-size: 13px;
  }

  .place-select-panel {
    padding: 7px;
  }

  .place-select-search {
    padding: 9px 10px;
    font-size: 12px;
  }

  .place-select-option {
    font-size: 13px;
    padding: 8px 9px;
  }

  .section-title {
    font-size: 18px;
    margin-top: 18px;
  }

  .payment-box {
    flex-direction: column;
  }

  .payment-card {
    padding: 11px 12px;
    gap: 10px;
  }

  .payment-title {
    font-size: 14px;
  }

  .placeorder-btn {
    font-size: 12px;
    padding: 13px;
  }

  .placeorder-right h3 {
    font-size: 24px;
  }

  .checkout-assurance {
    grid-template-columns: 1fr;
  }

  .checkout-assurance span {
    text-align: left;
    border-radius: 10px;
  }
}

@media (max-width: 375px) {
  .placeorder-intro h1 {
    font-size: 26px;
  }

  .placeorder-left,
  .placeorder-right {
    padding: 14px;
  }
}
