.watch-modal {
  position: fixed;
  inset: 0;
  background: radial-gradient(circle at 40% 28%, rgba(42, 18, 8, 0.5) 0%, rgba(8, 6, 6, 0.88) 58%, rgba(5, 5, 5, 0.94) 100%);
  backdrop-filter: blur(6px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
}

.modal-card {
  position: relative;
  width: min(440px, 94vw);
  height: min(780px, 90vh);
  background: linear-gradient(180deg, #fffefa 0%, #fff8ef 100%);
  border: 1px solid #ead6bf;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 30px 70px rgba(0, 0, 0, 0.52);
}

.modal-media {
  position: relative;
  height: calc(100% - 172px);
}

.modal-card video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.modal-pill {
  position: absolute;
  top: 14px;
  left: 14px;
  border: 1px solid rgba(255, 255, 255, 0.45);
  border-radius: 999px;
  background: rgba(20, 12, 8, 0.38);
  color: #fff;
  font-size: 10px;
  letter-spacing: 1px;
  text-transform: uppercase;
  padding: 6px 10px;
  backdrop-filter: blur(4px);
}

.modal-bottom {
  position: absolute;
  bottom: 0;
  min-height: 172px;
  width: 100%;
  background: linear-gradient(180deg, #fffefb 0%, #fff7ed 100%);
  border-top: 1px solid #ecdac7;
  padding: 14px 14px 16px;
  text-align: center;
}

.modal-title {
  font-family: "Times New Roman", serif;
  font-size: 33px;
  line-height: 1.1;
  color: #1f130d;
  font-weight: 600;
  margin: 0;
}

.modal-price-row {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 9px;
  margin-top: 8px;
}

.modal-price {
  font-size: 20px;
  font-weight: 700;
  color: #1d120d;
}

.modal-discount {
  font-size: 12px;
  color: #8b0d2b;
  border: 1px solid rgba(139, 13, 43, 0.3);
  border-radius: 999px;
  background: rgba(139, 13, 43, 0.06);
  padding: 3px 7px;
}

.modal-old-price {
  font-size: 14px;
  color: #8e8278;
  text-decoration: line-through;
}

.modal-counter {
  margin: 8px 0 10px;
  font-size: 11px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #7a6c61;
}

.buy-btn {
  width: 100%;
  padding: 13px;
  background: #8b0d2b;
  color: #fff;
  border: none;
  border-radius: 12px;
  font-size: 12px;
  letter-spacing: 1px;
  text-transform: uppercase;
  cursor: pointer;
  transition: background 0.2s ease, transform 0.2s ease;
}

.buy-btn:hover {
  background: #6f0a22;
  transform: translateY(-1px);
}

.close {
  position: absolute;
  top: 16px;
  right: 16px;
  width: 40px;
  height: 40px;
  border-radius: 999px;
  border: 1px solid rgba(255, 255, 255, 0.35);
  background: rgba(15, 11, 11, 0.52);
  color: #fff;
  font-size: 22px;
  line-height: 1;
  display: grid;
  place-items: center;
  cursor: pointer;
  z-index: 30;
  transition: background 0.2s ease, transform 0.2s ease;
}

.close:hover {
  background: rgba(15, 11, 11, 0.72);
  transform: scale(1.04);
}

.nav {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 46px;
  height: 46px;
  border-radius: 50%;
  background: rgba(18, 13, 10, 0.62);
  color: #fff;
  border: 1px solid rgba(255, 255, 255, 0.24);
  font-size: 26px;
  cursor: pointer;
  z-index: 25;
  transition: background 0.2s ease, transform 0.2s ease;
}

.nav:hover {
  background: rgba(18, 13, 10, 0.8);
  transform: translateY(-50%) scale(1.04);
}

.nav.left {
  left: max(12px, calc(50% - 292px));
}

.nav.right {
  right: max(12px, calc(50% - 292px));
}

@media (max-width: 900px) {
  .nav.left {
    left: 16px;
  }

  .nav.right {
    right: 16px;
  }
}

@media (max-width: 560px) {
  .modal-card {
    width: min(420px, 94vw);
    height: min(700px, 84vh);
    border-radius: 16px;
  }

  .modal-media {
    height: calc(100% - 156px);
  }

  .modal-bottom {
    min-height: 156px;
    padding: 12px 12px 14px;
  }

  .modal-title {
    font-size: 25px;
  }

  .modal-price {
    font-size: 18px;
  }

  .nav {
    width: 42px;
    height: 42px;
  }

  .nav.left {
    left: 8px;
  }

  .nav.right {
    right: 8px;
  }

  .close {
    top: 10px;
    right: 10px;
    width: 36px;
    height: 36px;
  }
}
