.cart-overlay {
  position: fixed;
  inset: 0;
  z-index: 1999;
  display: flex;
  justify-content: flex-end;
  background: rgba(0, 0, 0, 0.46);
  backdrop-filter: blur(3px);
  overscroll-behavior: contain;
}

.cart-drawer {
  position: fixed;
  top: 0;
  right: 0;
  width: min(360px, 100vw);
  height: 100dvh;
  display: flex;
  flex-direction: column;
  background: linear-gradient(180deg, #fbf8f3 0%, #f6efe4 100%);
  border-left: 1px solid #e6d6c2;
  box-shadow: -20px 0 40px rgba(35, 16, 6, 0.18);
  box-sizing: border-box;
  padding: 14px;
  animation: drawer-slide-in 280ms ease;
}

@keyframes drawer-slide-in {
  from {
    transform: translateX(100%);
  }

  to {
    transform: translateX(0);
  }
}

.drawer-head {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 10px;
  margin-bottom: 10px;
}

.drawer-head-copy h3 {
  margin: 0;
  font-size: 21px;
  line-height: 1;
  letter-spacing: 0.5px;
  color: #1b1511;
}

.drawer-head-copy p {
  margin: 4px 0 0;
  color: #715f54;
  font-size: 12px;
  letter-spacing: 0.8px;
  text-transform: uppercase;
}

.drawer-close {
  width: 30px;
  height: 30px;
  border: 1px solid #b89b86;
  border-radius: 999px;
  background: #fff;
  color: #381d13;
  font-size: 15px;
  line-height: 1;
  cursor: pointer;
  transition: all 180ms ease;
}

.drawer-close:hover {
  background: #8f0d36;
  border-color: #8f0d36;
  color: #fff;
}

.drawer-items {
  flex: 1;
  overflow-y: auto;
  padding-right: 4px;
}

.drawer-items::-webkit-scrollbar {
  width: 6px;
}

.drawer-items::-webkit-scrollbar-thumb {
  background: #ceb29b;
  border-radius: 999px;
}

.drawer-item {
  display: grid;
  grid-template-columns: 72px minmax(0, 1fr) auto;
  align-items: start;
  gap: 10px;
  margin-bottom: 8px;
  padding: 8px;
  border: 1px solid #e9dbc8;
  border-radius: 14px;
  background: rgba(255, 255, 255, 0.88);
}

.drawer-item img {
  width: 72px;
  height: 92px;
  border-radius: 9px;
  object-fit: cover;
  border: 1px solid #efe2d0;
}

.drawer-text {
  min-width: 0;
  padding-right: 0;
}

.drawer-text h4 {
  margin: 0;
  font-size: 15px;
  line-height: 1.15;
  letter-spacing: 0.35px;
  text-transform: uppercase;
  color: #13100d;
  max-width: 100%;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.drawer-size {
  margin: 6px 0 0;
  color: #6d5a4f;
  font-size: 10px;
  letter-spacing: 0.85px;
  text-transform: uppercase;
}

.drawer-size span {
  color: #201b17;
  font-weight: 600;
  margin-left: 6px;
}

.drawer-meta-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 8px;
  margin-top: 7px;
}

.drawer-qty {
  display: inline-flex;
  align-items: center;
  border: 1px solid #dbc6ae;
  border-radius: 999px;
  background: #fff;
  overflow: hidden;
}

.drawer-qty button {
  width: 27px;
  height: 27px;
  border: none;
  background: transparent;
  color: #2b2119;
  font-size: 15px;
  cursor: pointer;
  transition: background 160ms ease;
}

.drawer-qty button:hover {
  background: #f1e5d7;
}

.drawer-qty span {
  min-width: 20px;
  text-align: center;
  color: #1f1712;
  font-size: 12px;
  font-weight: 600;
}

.drawer-price {
  color: #8f0d36;
  font-size: 17px;
  font-weight: 700;
  white-space: nowrap;
}

.drawer-remove {
  grid-column: 3;
  grid-row: 1;
  justify-self: end;
  align-self: start;
  width: 22px;
  height: 22px;
  border: none;
  border-radius: 999px;
  background: #1b1511;
  color: #fff;
  font-size: 11px;
  line-height: 1;
  cursor: pointer;
  opacity: 0.88;
  transition: all 180ms ease;
}

.drawer-remove:hover {
  background: #8f0d36;
  opacity: 1;
}

.drawer-footer {
  margin-top: 6px;
  padding-top: 10px;
  border-top: 1px solid #d9c9b6;
}

.drawer-total {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 8px;
  padding: 9px 10px;
  border-radius: 12px;
  background: #f2e6d7;
  border: 1px solid #e2d0bb;
  color: #281d16;
}

.drawer-total span {
  font-size: 12px;
  letter-spacing: 1.2px;
  text-transform: uppercase;
}

.drawer-total strong {
  font-size: 19px;
  line-height: 1;
  color: #8f0d36;
}

.drawer-btn {
  width: 100%;
  border-radius: 9px;
  padding: 10px 12px;
  border: 1px solid transparent;
  font-size: 11px;
  font-weight: 600;
  letter-spacing: 1.4px;
  text-transform: uppercase;
  cursor: pointer;
  transition: all 200ms ease;
}

.drawer-btn + .drawer-btn {
  margin-top: 8px;
}

.drawer-btn-secondary {
  background: transparent;
  color: #261d17;
  border-color: #1f1813;
}

.drawer-btn-secondary:hover {
  background: #1f1813;
  color: #fff;
}

.drawer-btn-primary {
  background: linear-gradient(135deg, #7f0d32 0%, #a71043 100%);
  color: #fff;
  border-color: #7f0d32;
  box-shadow: 0 10px 20px rgba(127, 13, 50, 0.25);
}

.drawer-btn-primary:hover {
  filter: brightness(1.06);
  transform: translateY(-1px);
}

.empty-cart {
  height: calc(100dvh - 120px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.empty-title {
  margin: 0;
  color: #2d241e;
  font-size: 24px;
  letter-spacing: 0.2px;
}

.empty-copy {
  margin: 10px 0 20px;
  max-width: 270px;
  color: #6f5d52;
  line-height: 1.5;
  font-size: 14px;
}

.empty-cart .drawer-btn {
  max-width: 280px;
}

@media (max-width: 640px) {
  .cart-drawer {
    width: 100vw;
    padding: 10px;
  }

  .drawer-head-copy h3 {
    font-size: 18px;
  }

  .drawer-close {
    width: 28px;
    height: 28px;
    font-size: 14px;
  }

  .drawer-item {
    grid-template-columns: 64px minmax(0, 1fr) auto;
    gap: 8px;
    padding: 8px;
  }

  .drawer-item img {
    width: 64px;
    height: 82px;
  }

  .drawer-text h4 {
    font-size: 13px;
    line-height: 1.1;
    -webkit-line-clamp: 1;
  }

  .drawer-size {
    margin-top: 5px;
    font-size: 9px;
    letter-spacing: 0.75px;
  }

  .drawer-meta-row {
    margin-top: 6px;
    gap: 6px;
  }

  .drawer-qty button {
    width: 24px;
    height: 24px;
    font-size: 14px;
  }

  .drawer-qty span {
    min-width: 18px;
    font-size: 11px;
  }

  .drawer-price {
    font-size: 14px;
  }

  .drawer-total strong {
    font-size: 16px;
  }

  .drawer-btn {
    padding: 9px 10px;
    font-size: 10px;
    letter-spacing: 1.1px;
  }
}
