.wishlist-page {
  background: #ffffff;
  padding: 50px 20px 80px;
}

.wishlist-wrapper {
  max-width: 1200px;
  margin: 0 auto;
}

.wishlist-title {
  text-align: center;
  font-family: "Times New Roman", serif;
  font-size: 28px;
  letter-spacing: 1.8px;
  color: #8b0d2b;
  margin-bottom: 36px;
}

.wishlist-empty {
  position: relative;
  overflow: hidden;
  text-align: center;
  color: #6c5d52;
  font-size: 15px;
  background:
    radial-gradient(circle at 12% -18%, rgba(139, 13, 43, 0.13), transparent 42%),
    radial-gradient(circle at 92% 12%, rgba(201, 135, 61, 0.16), transparent 36%),
    linear-gradient(180deg, #fff8f4 0%, #f8f1e8 100%);
  border: 1px solid #eadfd6;
  padding: 42px 24px 36px;
  border-radius: 24px;
  box-shadow: 0 16px 36px rgba(47, 25, 12, 0.09);
}

.wishlist-empty::before {
  content: "";
  position: absolute;
  left: -96px;
  bottom: -116px;
  width: 220px;
  height: 220px;
  border-radius: 999px;
  background: radial-gradient(circle, rgba(127, 13, 50, 0.09) 0%, rgba(127, 13, 50, 0) 72%);
  pointer-events: none;
}

.wishlist-empty-icon {
  width: 62px;
  height: 62px;
  margin: 0 auto 12px;
  border-radius: 50%;
  border: 1px solid #dcc8b5;
  display: grid;
  place-items: center;
  color: #8b0d2b;
  font-size: 17px;
  font-weight: 700;
  letter-spacing: 1px;
  text-transform: uppercase;
  background: #fff;
  box-shadow: 0 10px 24px rgba(139, 13, 43, 0.14);
}

.wishlist-empty-kicker {
  margin: 0;
  font-size: 11px;
  letter-spacing: 2.1px;
  text-transform: uppercase;
  color: #8b0d2b;
}

.wishlist-empty h4 {
  margin: 10px 0 6px;
  font-size: 38px;
  line-height: 1.04;
  color: #2a1d17;
  font-weight: 500;
}

.wishlist-empty p {
  margin: 0 0 22px;
  font-size: 17px;
}

.wishlist-empty-actions {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  flex-wrap: wrap;
}

.wishlist-empty-btn {
  padding: 12px 28px;
  border: 1px solid #8b0d2b;
  background: #8b0d2b;
  color: #fff;
  cursor: pointer;
  font-size: 12px;
  letter-spacing: 1.8px;
  text-transform: uppercase;
  border-radius: 999px;
  transition: transform 0.2s ease, background 0.2s ease, box-shadow 0.2s ease;
  box-shadow: 0 12px 24px rgba(127, 13, 50, 0.24);
}

.wishlist-empty-btn.secondary {
  background: transparent;
  color: #2b2018;
  border-color: #2b2018;
  box-shadow: none;
}

.wishlist-empty-btn:hover {
  background: #6f0a22;
  transform: translateY(-1px);
  box-shadow: 0 14px 24px rgba(127, 13, 50, 0.26);
}

.wishlist-empty-btn.secondary:hover {
  background: #2b2018;
  color: #fff;
  box-shadow: none;
}

.wishlist-list {
  display: flex;
  flex-direction: column;
  gap: 22px;
}

.wishlist-item {
  position: relative;
  display: grid;
  grid-template-columns: 140px 1fr auto;
  gap: 20px;
  align-items: center;
  padding: 16px 0;
  border-bottom: 1px solid #eee;
  transition: transform 0.25s ease, box-shadow 0.25s ease;
}

.wishlist-item:hover {
  transform: translateY(-2px);
}

.wishlist-remove {
  position: absolute;
  top: 6px;
  right: 0;
  border: none;
  background: none;
  font-size: 20px;
  cursor: pointer;
}

.wishlist-image {
  cursor: pointer;
}

.wishlist-image img {
  width: 100%;
  max-width: 140px;
  height: 160px;
  object-fit: cover;
  display: block;
  border-radius: 10px;
}

.wishlist-info h4 {
  font-size: 16px;
  margin-bottom: 8px;
  cursor: pointer;
  color: #1f1a17;
}

.wishlist-size {
  font-size: 13px;
  color: #666;
  margin-bottom: 6px;
}

.wishlist-price {
  display: flex;
  gap: 12px;
  align-items: center;
  font-size: 14px;
}

.wishlist-price del {
  color: #888;
}

.wishlist-actions {
  display: flex;
  align-items: center;
  gap: 12px;
}

.wishlist-qty {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  padding: 6px 8px;
  border-radius: 999px;
  background: #f6f5f5;
}

.wishlist-qty button {
  width: 24px;
  height: 24px;
  border: 1px solid #f6f5f5;
  background: #f6f5f5;
  border-radius: 50%;
  cursor: pointer;
  font-size: 16px;
  line-height: 1;
}

.wishlist-qty span {
  min-width: 16px;
  text-align: center;
  font-size: 13px;
}

.wishlist-cart-btn {
  padding: 10px 24px;
  border: 1px solid #000;
  background: transparent;
  cursor: pointer;
  font-size: 13px;
  letter-spacing: 1.2px;
  text-transform: uppercase;
  transition: transform 0.2s ease, background 0.2s ease, color 0.2s ease;
}

.wishlist-cart-btn:hover {
  background: #000;
  color: #fff;
}

.wishlist-cart-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

@media (max-width: 768px) {
  .wishlist-empty {
    padding: 28px 16px 24px;
    border-radius: 18px;
  }

  .wishlist-empty h4 {
    font-size: 30px;
  }

  .wishlist-empty p {
    font-size: 15px;
  }

  .wishlist-empty-btn {
    width: 100%;
    max-width: 320px;
  }

  .wishlist-empty-actions {
    gap: 8px;
  }

  .wishlist-item {
    grid-template-columns: 90px 1fr;
    grid-template-rows: auto auto;
  }

  .wishlist-actions {
    grid-column: 1 / -1;
    justify-content: flex-start;
    flex-wrap: wrap;
  }

  .wishlist-remove {
    top: 2px;
  }
}
