.category-section {
  --cat-maroon: #8b0d2b;
  --cat-maroon-deep: #6f0a22;
  --cat-gold: #c89a58;
  position: relative;
  padding: 58px 16px 54px;
  background:
    radial-gradient(circle at 12% 20%, rgba(242, 218, 185, 0.38) 0%, rgba(242, 218, 185, 0) 44%),
    linear-gradient(180deg, #fff7ef 0%, #fffdf9 48%, #ffffff 100%);
  overflow: hidden;
}

.category-head {
  max-width: 760px;
  margin: 0 auto 28px;
  text-align: center;
}

.category-kicker {
  margin: 0;
  font-size: 11px;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: var(--cat-maroon);
}

.category-head h2 {
  margin: 8px 0 8px;
  font-family: "Times New Roman", serif;
  font-size: clamp(32px, 4vw, 46px);
  line-height: 1.02;
  color: #1f130e;
}

.category-subtitle {
  margin: 0;
  font-size: 14px;
  line-height: 1.58;
  color: #6e5f53;
}

.category-wrapper {
  max-width: 1240px;
  margin: 0 auto;
  position: relative;
}

.category-grid {
  display: grid;
  grid-template-columns: repeat(5, minmax(0, 1fr));
  gap: 18px;
}

.category-card {
  border: 1px solid #dbdbda;
  border-radius: 20px;
  background: linear-gradient(180deg, #fffdfb 50%, #fff9f3 100%);
  box-shadow: 0 12px 26px rgba(37, 20, 10, 0.12);
  cursor: pointer;
  overflow: hidden;
  padding: 0;
  text-align: left;
  transition: transform 0.25s ease, box-shadow 0.25s ease, border-color 0.25s ease;
}

.category-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 18px 30px rgba(37, 20, 10, 0.18);
  border-color: #dcbc95;
}

.category-media {
  display: block;
  position: relative;
  height: 340px;
  overflow: hidden;
}

.category-media img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  transition: transform 0.38s ease;
}

.category-card:hover .category-media img {
  transform: scale(1.05);
}

.category-shade {
  position: absolute;
  inset: 0;
  background: linear-gradient(180deg, rgba(26, 13, 7, 0.06) 38%, rgba(20, 11, 7, 0.5) 100%);
}

.category-chip {
  position: absolute;
  right: 10px;
  bottom: 10px;
  font-size: 10px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #fff;
  border: 1px solid rgba(255, 255, 255, 0.42);
  border-radius: 999px;
  padding: 5px 9px;
  background: rgba(24, 15, 9, 0.35);
  backdrop-filter: blur(3px);
}

.category-copy {
  display: block;
  padding: 12px 12px 14px;
}

.category-name {
  display: block;
  font-family: "Times New Roman", serif;
  font-size: 17px;
  font-weight: 600;
  line-height: 1.1;
  color: #1f140e;
}

.category-tagline {
  display: block;
  margin-top: 4px;
  font-size: 12px;
  color: #7a6c61;
}

.cat-arrow {
  position: absolute;
  top: 44%;
  transform: translateY(-50%);
  width: 40px;
  height: 40px;
  border-radius: 999px;
  border: 1px solid #e5d4c3;
  background: rgba(255, 255, 255, 0.96);
  color: #33231b;
  font-size: 24px;
  line-height: 1;
  display: none;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  box-shadow: 0 9px 20px rgba(28, 15, 8, 0.2);
  z-index: 5;
}

.cat-arrow.left {
  left: 4px;
}

.cat-arrow.right {
  right: 4px;
}

.cat-arrow:hover {
  background: #fff;
}

@media (max-width: 1080px) {
  .category-grid {
    grid-template-columns: repeat(3, minmax(0, 1fr));
    gap: 16px;
  }
}

@media (max-width: 768px) {
  .category-section {
    padding: 44px 0 38px;
  }

  .category-head {
    margin: 0 auto 18px;
    padding: 0 14px;
  }

  .category-subtitle {
    font-size: 13px;
  }

  .category-wrapper {
    max-width: 100%;
  }

  .category-grid {
    display: flex;
    gap: 12px;
    overflow-x: auto;
    justify-content: flex-start;
    width: 100%;
    padding: 6px 16px 12px;
    margin: 0;
    scroll-snap-type: x mandatory;
    -webkit-overflow-scrolling: touch;
  }

  .category-grid::-webkit-scrollbar {
    display: none;
  }

  .category-card {
    width: 166px;
    flex-shrink: 0;
    scroll-snap-align: start;
    border-radius: 16px;
  }

  .category-media {
    height: 248px;
  }

  .category-name {
    font-size: 15px;
  }

  .category-tagline {
    font-size: 11px;
  }

  .cat-arrow {
    display: flex;
    width: 38px;
    height: 38px;
    top: 47%;
  }

  .cat-arrow.left {
    left: 8px;
  }

  .cat-arrow.right {
    right: 8px;
  }
}

@media (max-width: 480px) {
  .category-head h2 {
    font-size: 32px;
  }

  .category-kicker {
    font-size: 10px;
  }

  .category-grid {
    padding-bottom: 8px;
  }

  .category-card {
    width: 156px;
  }

  .category-media {
    height: 232px;
  }
}
