.footer {
  --footer-bg-1: #2f1a1f;
  --footer-bg-2: #4a262d;
  --footer-accent: #c79a58;
  --footer-maroon: #8b0d2b;
  --footer-ink: #1f130e;
  background: linear-gradient(
    120deg,
    #f2ddc7 0%,
    #e5c2a4 18%,
    var(--footer-bg-2) 40%,
    #5d323a 100%
  );
  color: #fff;
  font-family: "Times New Roman", serif;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: 1;
  transform: translateZ(0);
  will-change: transform;
  backface-visibility: hidden;
}

.footer-inner {
  position: relative;
}

.footer-inner::before,
.footer-inner::after {
  content: "";
  position: absolute;
  border-radius: 999px;
  pointer-events: none;
}

.footer-inner::before {
  width: 300px;
  height: 300px;
  top: -110px;
  left: -80px;
  background: radial-gradient(circle, rgba(199, 154, 88, 0.2) 0%, rgba(199, 154, 88, 0) 70%);
}

.footer-inner::after {
  width: 260px;
  height: 260px;
  right: -90px;
  bottom: 30px;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.09) 0%, rgba(255, 255, 255, 0) 72%);
}

.footer-top {
  max-width: 1280px;
  margin: 0 auto;
  padding: 60px 38px 46px;
  display: grid;
  grid-template-columns: minmax(200px, 0.95fr) minmax(320px, 1.25fr) minmax(240px, 0.85fr);
  column-gap: 34px;
  row-gap: 28px;
  align-items: start;
  position: relative;
  z-index: 1;
}

.footer-brand {
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.footer-logo {
  margin-top: 2px;
}

.footer-logo img {
  width: 126px;
  max-width: 100%;
  filter: drop-shadow(0 5px 10px rgba(28, 14, 8, 0.2));
}

.footer-brand-copy {
  margin: 0;
  max-width: 26ch;
  font-size: 14px;
  line-height: 1.6;
  color: #4b3324;
}

.footer-socials {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.footer-socials a {
  text-decoration: none;
  color: #5a3529;
  border: 1px solid #d8b391;
  border-radius: 999px;
  width: 34px;
  height: 34px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 15px;
  background: rgba(255, 255, 255, 0.74);
  transition: background 0.2s ease, border-color 0.2s ease, transform 0.2s ease;
}

.footer-socials a:hover {
  background: #fff;
  border-color: #bb8b64;
  transform: translateY(-1px);
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

.footer-subscribe {
  border: 1px solid rgba(255, 255, 255, 0.24);
  border-radius: 18px;
  padding: 20px 20px 18px;
  background: rgba(255, 255, 255, 0.07);
  backdrop-filter: blur(4px);
}

.footer-kicker {
  margin: 0;
  color: #f1d7ad;
  font-size: 11px;
  letter-spacing: 1.8px;
  text-transform: uppercase;
}

.footer-subscribe h3 {
  margin: 8px 0 14px;
  font-size: 28px;
  font-weight: 500;
  line-height: 1.16;
  color: #fff7ec;
  max-width: 18ch;
}

.subscribe-input {
  display: grid;
  grid-template-columns: 1fr auto;
  gap: 8px;
}

.subscribe-input input {
  width: 100%;
  background: rgba(255, 255, 255, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.38);
  border-radius: 10px;
  padding: 11px 12px;
  font-size: 14px;
  color: #fff;
  outline: none;
}

.subscribe-input input::placeholder {
  color: rgba(255, 245, 236, 0.78);
}

.subscribe-input input:focus {
  border-color: #f0cf9a;
  box-shadow: 0 0 0 2px rgba(240, 207, 154, 0.2);
}

.subscribe-btn {
  border: 1px solid var(--footer-accent);
  border-radius: 10px;
  background: var(--footer-accent);
  color: var(--footer-ink);
  padding: 0 14px;
  font-size: 12px;
  letter-spacing: 0.9px;
  text-transform: uppercase;
  cursor: pointer;
  transition: background 0.2s ease, transform 0.2s ease;
}

.subscribe-btn:hover {
  background: #d9ae72;
  transform: translateY(-1px);
}

.footer-sub-note {
  margin: 10px 0 0;
  font-size: 11px;
  line-height: 1.45;
  color: rgba(255, 236, 218, 0.76);
}

.footer-links-grid {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 22px;
}

.footer-links h4 {
  margin: 0 0 10px;
  font-size: 12px;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #f0d6af;
}

.footer-links ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-links li {
  margin-bottom: 9px;
  font-size: 15px;
}

.footer-links a {
  text-decoration: none;
  color: rgba(255, 246, 236, 0.92);
  transition: color 0.2s ease, text-decoration-color 0.2s ease;
  text-underline-offset: 3px;
}

.footer-links a:hover {
  color: #ffffff;
  text-decoration: underline;
  text-decoration-color: rgba(255, 255, 255, 0.45);
}

.footer-bottom {
  background: rgba(16, 10, 10, 0.72);
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  text-align: center;
  font-size: 12px;
  letter-spacing: 0.7px;
  color: rgba(255, 239, 224, 0.88);
  padding: 12px 14px;
  position: relative;
  z-index: 1;
}

@media (max-width: 1100px) {
  .footer-top {
    grid-template-columns: 1fr 1fr;
    row-gap: 26px;
  }

  .footer-links-grid {
    grid-column: 1 / -1;
    max-width: 520px;
  }
}

@media (max-width: 768px) {
  .footer {
    position: static;
    transform: none;
    background:
      radial-gradient(
        circle at 14% 12%,
        rgba(255, 239, 216, 0.52) 0%,
        rgba(255, 239, 216, 0) 44%
      ),
      linear-gradient(
        155deg,
        #f3dfca 0%,
        #e7c8aa 36%,
        #8a4e50 70%,
        #603242 100%
      );
  }

  .footer-inner::before,
  .footer-inner::after {
    display: none;
  }

  .footer-top {
    grid-template-columns: 1fr;
    padding: 40px 18px 30px;
    row-gap: 20px;
    justify-items: center;
    text-align: center;
  }

  .footer-brand {
    align-items: center;
  }

  .footer-brand-copy {
    max-width: none;
    text-align: center;
  }

  .footer-socials {
    justify-content: center;
  }

  .footer-subscribe {
    width: 100%;
    max-width: 420px;
    text-align: center;
  }

  .footer-kicker {
    text-align: center;
  }

  .footer-subscribe h3 {
    font-size: 22px;
    max-width: none;
    color: #fff9f1;
    text-shadow: 0 1px 0 rgba(18, 8, 8, 0.2);
  }

  .footer-subscribe {
    border-color: rgba(255, 236, 216, 0.34);
    background: linear-gradient(
      180deg,
      rgba(106, 61, 71, 0.58) 0%,
      rgba(88, 49, 60, 0.64) 100%
    );
    box-shadow:
      0 10px 24px rgba(25, 13, 12, 0.2),
      inset 0 1px 0 rgba(255, 255, 255, 0.14);
    backdrop-filter: blur(2px);
  }

  .footer-kicker {
    color: #f4dbb5;
  }

  .subscribe-input input {
    background: rgba(255, 255, 255, 0.16);
    border-color: rgba(255, 255, 255, 0.5);
  }

  .subscribe-input input::placeholder {
    color: rgba(255, 245, 236, 0.95);
  }

  .footer-sub-note {
    color: rgba(255, 238, 222, 0.9);
    text-align: center;
  }

  .footer-links-grid {
    grid-template-columns: 1fr 1fr;
    gap: 16px;
    width: min(100%, 380px);
    margin: 0 auto;
  }

  .footer-links {
    text-align: left;
  }

  .footer-links ul {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  .footer-links li {
    font-size: 14px;
  }

  .footer-bottom {
    font-size: 11px;
    line-height: 1.4;
  }
}

@media (max-width: 520px) {
  .footer {
    background:
      radial-gradient(
        circle at 18% 10%,
        rgba(255, 240, 220, 0.5) 0%,
        rgba(255, 240, 220, 0) 42%
      ),
      linear-gradient(
        180deg,
        #f2ddc6 0%,
        #e4c4a7 34%,
        #81505a 68%,
        #5a3244 100%
      );
  }

  .footer-links-grid {
    grid-template-columns: repeat(2, minmax(120px, 1fr));
    gap: 14px;
    justify-items: stretch;
    width: min(100%, 340px);
  }

  .footer-subscribe {
    border-color: rgba(255, 236, 216, 0.4);
    background: linear-gradient(
      180deg,
      rgba(118, 72, 82, 0.62) 0%,
      rgba(91, 52, 65, 0.7) 100%
    );
  }

  .subscribe-input input {
    background: rgba(255, 255, 255, 0.2);
  }

  .subscribe-input {
    grid-template-columns: 1fr;
  }

  .subscribe-btn {
    width: 100%;
    min-height: 40px;
  }
}
